import Vue from 'vue'
import VueRouter from 'vue-router'

const Home = () => import('../views/Home')
const CoreBusiness = () => import('../views/CoreBusiness')
const ProductSolution = () => import('../views/ProductSolution')
const LatestCase = () => import('../views/LatestCase')
const IndustryNews = () => import('../views/IndustryNews')
const About = () => import('../views/About')

Vue.use(VueRouter)

export const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: '首页',
    // meta: {
    //   noScroll: true
    // },
    component: Home
  },
  {
    path: '/core-business',
    name: '核心业务',
    component: CoreBusiness
  },
  {
    path: '/product-solution',
    name: '产品与方案',
    component: ProductSolution
  },
  {
    path: '/latest-case',
    name: '最新案例',
    component: LatestCase
  },
  {
    path: '/industry-news',
    name: '行业洞察',
    component: IndustryNews
  },
  {
    path: '/about',
    name: '关于易享',
    component: About
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
