<template>
  <div id="app">
    <MLayout :isNormalHeader="isNormalHeader">
      <router-view />
    </MLayout>
  </div>
</template>

<script>
import { debounce } from '@/utils'
import MLayout from './components/MLayout'

export default {
  name: 'App',
  data() {
    return {
      scrollTop: 0
    }
  },
  computed: {
    isNormalHeader() {
      return this.$route.meta.noScroll || this.scrollTop > 240
    }
  },
  mounted() {
    window.addEventListener(
      'scroll',
      debounce(e => {
        this.scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      }),
      200
    )
  },
  components: {
    MLayout
  }
}
</script>
