<template>
  <div :class="{ 'pc-header': true, normal: isNormalHeader }">
    <div class="container">
      <div class="logo">
        <h1>易享科技</h1>
      </div>
      <div class="tabs">
        <div
          class="tab-item"
          :class="{ active: currIndex === index }"
          v-for="(tab, index) in tabs"
          :key="tab.path"
          @click="handleTabClick(index)"
        >
          {{ tab.name }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { routes } from '@/router'
const tabs = routes.filter(e => !e.redirect)
export default {
  name: 'PCHeader',
  props: {
    isNormalHeader: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      tabs,
      currIndex: 0
    }
  },
  methods: {
    handleTabClick(index) {
      this.currIndex = index
      this.$router.push(tabs[index].path)
    }
  }
}
</script>

